.group-result {
  max-width: 400px;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: -0.01em;
  text-transform: capitalize;
  border-bottom: 1px solid #9d9d9d;
  display: flex;
  justify-content: space-between;
}

.group-result-circle {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.negative {
  color: rgb(248, 25, 25);
}
